html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal; }

ul {
  list-style: none; }

button,
input,
select,
textarea {
  margin: 0; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

img,
video {
  height: auto;
  max-width: 100%; }

iframe {
  border: 0; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0;
  text-align: left; }

h1 {
  font-family: 'Jura', sans-serif;
  font-size: 2.5rem;
  text-transform: uppercase;
  letter-spacing: 0.03cm;
  color: #1b1b1b; }

h2 {
  font-family: 'Jura', sans-serif;
  font-size: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.03cm;
  color: #1b1b1b; }

h3 {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  font-weight: 500;
  text-transform: uppercase;
  color: #1b1b1b;
  margin-bottom: 0.5rem; }

h4 {
  font-family: 'Roboto', sans-serif;
  font-size: 0.8rem;
  font-weight: 400;
  font-style: italic;
  color: #9e9e9e; }

p {
  font-family: 'Cormorant Garamond', serif;
  font-weight: 400;
  color: #424242; }

html {
  font-size: 12px; }

body {
  margin: 0;
  background-color: #e9ebee;
  display: flex;
  min-height: 100vh;
  align-items: center; }

article.page {
  width: 8.27in;
  height: 11.69in;
  display: flex;
  flex-direction: column;
  padding: 14pt;
  background-color: #fff;
  margin: 2rem auto;
  position: relative; }
  article.page::before, article.page::after {
    z-index: -1;
    position: absolute;
    content: '';
    bottom: 1rem;
    width: 50%;
    top: 90%;
    background: #9e9e9e;
    box-shadow: 0 1rem 0.5rem #9e9e9e; }
  article.page::before {
    transform: rotate(-3deg);
    left: 0.5rem; }
  article.page::after {
    transform: rotate(3deg);
    right: 0.5rem; }
  article.page:not(.back) {
    padding-top: 0; }
  article.page main {
    display: flex;
    align-items: stretch;
    height: 100%; }

@media print {
  body {
    width: 8.27in;
    height: 11.69in; }
  article.page {
    margin: 0; }
  @page {
    size: auto;
    margin: 0; } }

.column--left, .column--right {
  flex-grow: 1;
  flex-basis: 0;
  padding: 14pt;
  padding-bottom: 0; }

.column--right {
  padding-right: 0; }
  .column--right .column__section__content {
    padding-left: 0.1rem; }

.column--left {
  padding-left: 0; }
  .column--left .column__section__content {
    padding-right: 0.1rem; }
  .column--left .column__section header {
    text-align: right; }

.column__section:not(:last-child) {
  margin-bottom: 4rem; }

.column__section__content {
  margin-top: 2rem; }
  .column__section__content > section:not(:first-child) {
    margin-top: 2rem; }

.column__section--with-subcolumns .column__section__content {
  display: flex;
  flex-wrap: wrap; }

.separator {
  width: 1px;
  height: 100%;
  display: block;
  background-color: #1b1b1b; }
